@media (min-width:768px) {
    .show__hideall {
        display: none;
        position: relative;
        cursor: pointer;
       }
    
       .show__hideall:before {
        position: absolute;
        content: "<<";
        left: -10px;  
        color: #41F6D7;
        width: 6px;
        transform: rotate(90deg);
        font-family: monospace;
        letter-spacing: -2.5px;
       }
}