.annotation {
    width: 500px;
    margin-top: 30px;
    line-height: 20px;
    letter-spacing: 0.2px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.annotation>div:first-child {
    margin-bottom: 20px;
}

@media (max-width: 768px) {
    .annotation{
        max-width: 350px;
        margin-left: 6% ;
    }
}

@media (min-width: 768px) {
    .annotation{
        margin-top: 50px;
        margin-left: 4%;
    }
}