.modal {
    background-color: #FFFFFF;
    margin: 0;
    padding: 15px;
    width: 320px;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
    display: none;
    position: fixed;
    z-index: 9999;
    height: 100%;
   
}

.modal__header {
  display:flex;
  justify-content: space-between;
  align-items: center;
}


.modal-active {
    display: block;
  }
  
.modal-hidden {
    display: none;
  }

.end {
  bottom: 0;
}

.modal-wrapper {
  background: #FFFFFF;
  position: relative;
}

.modal a {
  cursor: pointer;
}

.modal a:hover {
  text-decoration: underline;
}

  @media (max-width:768px) {
    .modal {
      width: 100%;
      display: none;
    }
  }

  @media (min-width:1120px) {
    .modal {
      display: block;
      box-shadow: none;
      position: fixed;
      width: 20%;
    }

    .CPS-main {
      width: 80%;
    }

    .hidemodal {
      display: none;
    }
  }
