* {
  box-sizing: border-box; }

@font-face {
  font-family: "TTlakes";
  src: url(./fonts/TTLakes-Light.woff) format("WOFF");
  font-style: normal;
  font-weight: smaller; }

body {
  overflow: visible;
  margin: 0;
  font-family: "TTLakes",sans-serif;
  min-width: 320px;
  background-color: #F8F8F8;
  color: #1B1C21;
  font-weight: 500; }

.CPS-main {
  margin: 0; }

:root {
  width: 100%; }

@media (min-width: 1120px) {
  .CPS-main {
    margin-left: 20%; } }
