.title {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 19px;
}

.title:before {
    position: absolute;
    content:"";
    border-right: 5px solid #41F6D7;
    border-radius: 16%;
    height: 38px;
    left:-3px;
}

.feedback-header.title:before {
    left: -16px;
}

.feedback-header.title {
    position: relative;
    padding: 70px 20px 0px 12px;
}


.feedback-header.title>img {
    position: absolute;
    top: 25px;
}


.feedback-header.title>h1 {
    font-size: 24px;
    font-weight: 1000;
    line-height: 32px;
    letter-spacing: -0.6px;
}

@media(min-width: 768px) {
    .feedback-header.title>img {
        position: absolute;
        top: 85px;
        left: -70px;
        filter: drop-shadow(0 0 0 rgb(181, 182, 182,1));
    }
}

@media (min-width:1120px) {
    .title {
        position: absolute;
        top: -80px;
        left: 40px;
    }

    .title:before {
        left: -40px;
    }

    .feedback-header.title {
        top: -15px;
        left: 0px;
    }
}