.services__read, .services__close{
    position: relative;
    margin-left: 30px;
}

.services__read {
    display: none;
}

.services__read a,.services__close a {
    text-decoration: none;
}

.services__read a:active,.services__close a:active{
    color: black;
}

.services__read a:visited,.services__close a:visited {
    color: black;
}

.services__read:before {
    position: absolute;
    content: ">>";
    top: -1px;
    left: -20px;   
    color: #41F6D7;
    width: 6px;
    transform: rotate(90deg);
    font-family: monospace;
    letter-spacing: -2.5px;
}

.services__close:before {
    position: absolute;
    content: ">>";
    top: 5px;
    left: -20px;
    color: #41F6D7;
    width: 6px;
    transform: rotate(-90deg);
    font-family: monospace;
    letter-spacing: -2.5px;
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
}