.swiper-container {
    position: relative;
    overflow: unset!important;
    list-style: none;
    padding: 0;
    z-index: 1;
    height: 195px;
}


.swiper-slide {
text-align: center;
font-size: 18px;
background: #fff;
width: 230px!important;

/* Center slide text vertically */
display: -webkit-box;
display: -ms-flexbox;
display: -webkit-flex;
display: flex;
-webkit-box-pack: center;
-ms-flex-pack: center;
-webkit-justify-content: center;
justify-content: center;
-webkit-box-align: center;
-ms-flex-align: center;
-webkit-align-items: center;
align-items: center;
}

.swiper-container{
    margin-top: 10px;
    height: 100%!important;
  }

  .swiper-pagination {
      display: flex!important;
      justify-content: center!important;
      position: relative!important;
      bottom: -20px !important;
  }
  
  


  .swiper-wrapper {
    overflow: overlay;
  }

  .swiper-slide {
    width: 190px!important;
    background-color: #ffffff;
    border: 2px solid #EAEAEA;
    border-radius: 6px;
    margin-left: 20px;
  }

  .brands .brands .swiper-slide {
    height: 165px !important;
  }

.swiper-pagination-bullet {
    width: 18px!important;
    height: 18px!important;
    line-height: 20px!important;
    font-size: 14px!important;
    background-color: #7E7E82!important;
}
    
.brands:nth-child(3) .brands-slide__item2{
  display: flex;
  align-items:flex-start;
  margin-top: 10px;
  padding-top: 25px;
 }

.swiper-pagination {
  z-index: 99999999;
  
}

 .brands .brands .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 10px 5px!important;
 }

 
 .brands:nth-child(3) .brands-slide__items {
  height: 183px;
}

 .brands:nth-child(2) .brands-slide__items {
  height: 100px;
}

/*.brands:nth-child(2) .swiper-pagination-bullet:nth-child(5), .brands:nth-child(2) .swiper-pagination-bullet:nth-child(6) {
 display: none;
}*/


@media (min-width:768px) {
    .swiper-pagination {
        display: none!important;
    } 

    .brands .brands .swiper-slide {
        width: 100%!important;
        height: 120px !important;
      }

    .brands .brands .swiper-wrapper {
        overflow: visible;
    }

    .swiper-wrapper {
        position: relative;
        width: 100%;
        z-index: 1;
        display: flex;
        transition-property: transform;
        box-sizing: content-box;
        overflow: hidden;

    }
    .swiper-container {
        margin-left: auto;
        position: relative;
        list-style: none;
        padding: 0;
        z-index: 1;
        height: 100%;
    }

    .brands .brands .brands-slide__items {
      height: 535px;
    }

}
