.btn-order {
    color: #ffffff;
    background-color: #FF3E79;
    width: 120px;
    height: 30px;
    border: 1px solid #FF3E79;
    border-radius: 16px;
    line-height: 24px;
    font-size: 14px;
    letter-spacing: -0.85px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-order:hover {
    cursor: pointer;
}

@media (max-width: 768px) {
    .btn-order {
        position: absolute;
        bottom: 10px;
        right: 13px;
    }
}

@media (min-width: 768px) {

    .brands .brands .brands-slide__item3:first-child .btn-order {
        opacity: 0;
    }

    .btn-order div {
        font-family: "TT Lakes" ;
        margin-left: 5px;
    }

    .btn-order img {
        margin-left: 8px;
        padding-top: 1px;
    }

    .brands .brands .brands-slide__item3 .btn-order:nth-child(0) {
        background: rgba(0, 0, 0, 0);
    }

}


