@media (max-width: 768px) {

  .brands-slide__item3 {
      margin: 16px 7px;
      height: 105px !important;
  }

  .brands .brands .brands-slide__item3:first-child {
    display: none;
  }

  .brands .brands .brands-slide__item3 {
    position: relative;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    display: flex;
    align-items: start;
    padding-left: 15px;
  }

  .brands .brands .brands-slide__item3>div {
    margin-bottom: 10px;
    text-align: left!important;
  }

  .brands .brands .brands-slide__item3>div>div:first-child {
    color: #7E7E82;
  }
}

@media (min-width: 768px) {

  .brands .brands .brands-slide__item3>div>div:first-child {
    display: none;
  }

   .brands .brands .swiper-slide {
       width: 100%!important;
   } 

  .brands .brands .brands-slide__item3 {
    position: relative;
    margin-top: -31px;
    border-top: none!important;
    border-radius:3!important;
    width: 100%!important;
    top: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 16px;
    letter-spacing: 0.2px;
    flex-grow: 0;
    padding: 0 30px;
    font-size: 16px;
    border: 1px solid transparent;
    border-radius: 10px;
  }   

  .brands .brands .brands-slide__item3:first-child {
    display: flex;
    background-color:  #F8F8F8;
    border: none;
    color: #7E7E82;
  }

  .brands .brands .brands-slide__item3:first-child:after,.brands .brands .brands-slide__item3:last-child:after  {
    display: none;
  }

  .brands .brands .brands-slide__item3>div {
     flex-grow:1;
     text-align: left;
     max-width: 20%;
     min-width: 20%;
  }

  .brands .brands .brands-slide__item3>div:nth-child(1) {
    flex-grow:3;
    text-align: left;
    max-width: 46%;
    min-width: 46%;
    margin-left: 10px;
  }

  .brands .brands .brands-slide__item3:after {
    top: 105px;
    position: absolute;
    content: "";
    border-bottom: 2px solid #EAEAEA;
    width: 97%;
    z-index: 333;
  }

}