.services__logo {
    position: relative;
}

.services__logo img {
    margin-right: 20px;
    width: 97px;
    height: 51px;
}

@media (min-width:768px) {

    .services__logo:before {
        position: absolute;
        content:"";
        border-left: 2px solid #EAEAEA;
        height: 28px;
        top: 12px;
        left: -3px;
    }
}

@media (min-width:1120px) {
    .services__logo {
        display: none;
    }
}