.menu-item {
    display: flex;
    flex-shrink: 0;
    width: 271px;
    height: 38px;
    align-items: center;
    color: #7E7E82;
    justify-content: center;
    margin-left: 10px;
}

.menu-item > label:hover {
    cursor: pointer;
}


.menu-item > label {
    display: block;
    user-select: none;
    text-align: center;
}

.active {
    background-color: #ffff;
    border: 2px solid #B8FFEC;
    border-radius: 6px;
}

.hidden {
    display: none;
}

.menu-item > input {
    z-index: -2;
    opacity: 0;
}

.menu-item > input + label {
    cursor: pointer;
}

.menu-item:nth-child(3) {
    width: 200px;
}


@media(min-width: 1120px) {
    .menu-item:last-child {
        margin-right: 95px;
    }
}