.modal-feedback {
    background-color: #ffffff;
    position: fixed;
    top: 0;
    right: 0;
    min-width: 320px;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
    z-index: 9999999;
    width: 100%;
    padding: 13px 16px;
    display: flex;
    flex-direction: column;
    display: none;
    height: 100vh;
}

.modal-feedback:nth-child(1) {
    min-height: 100%;
}


.modal-feedback ul {
    width: 100%;
    margin-right: 10px;
    position: relative;
    margin: auto;
    left: -27px;
} 

.modal-feedback li {
    list-style-type: none;
    position: relative;
}
.modal-feedback input,.modal-feedback textarea  {
    width: 98%;
    height: 50px;
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    margin: auto;
    margin-bottom: 16px;
    font-size: 14px;
    color: #767777;
    padding-left: 16px;
}

.modal-feedback textarea {
    height: 120px;
    resize: none;
    font-family: "TTLakes";
    font-weight: 700;
    padding-top: 16px;

}

.modal-feedback .form-text {
    width: 288px;
    font-size: 13px;
    font-family: sans-serif;
    color: #7E7E82;
}

.modal-feedback .form-text span {
    color: #FF3E79;
}

.modal-feedback .btn-container {
    display: flex;
    justify-content: end;
    margin-top: 11px;
}

.modal-feedback .btn-container .btn-order {
    position: relative;
}


@media(min-width: 768px) {
    .modal-feedback,.modal-feedback:nth-child(1) {
        width: 320px;
        min-height: 450px;
    }
}

@media(min-width: 1120px) {
    .modal-feedback,.modal-feedback:nth-child(1) {
        width: 440px;
        top: 0;
    }
}






