.items-services {
    position: relative;
    margin-right: 35px;
    display: flex;
    width: 110px;
}

.items-services:before {
    position: absolute;
    content:"";
    border-left: 2px solid #EAEAEA;
    height: 28px;
    top: 6px;
    left: 6px;
}

.items-services p {
    display: none;
}

.items-services img:last-child {
    margin-left: 12px;
}

@media (min-width:768px) {
    .leftitems-services {
        display: flex;
    }
}

@media (min-width:1120px) {
    .items-services:before{
        display: none;
    }

    .items-services {
       width: auto;
    }

    .items-services__item p {
        display: block;
        margin-right: 10px;
    }

    .items-services img:last-child {
        margin-top: 7px;
        margin-right: 12px;
    }
}