.modal__menu ul{
    display: flex;
    flex-direction: column;
}

.modal__menu ul > li {
    position: relative;
    margin-left: -30px;
    margin-top: 22px;
    list-style-type: none;
    cursor: pointer;
    transition: all 0.2s ease-out;
}

.modal__menu ul > li:hover {
    color:#8c8c97;
}

.modal__menu--selected:before {
    position: absolute;
    content: '';
    border-left: 5px solid #41F6D7;
    height: 37px;
    top: -7px;
    left: -25px;
    transition: all 0.2 ease-out;
}