.brands__show {
    display: none;
    position: relative;
    flex-grow: 1;
    margin-top: 17px;
    margin-left: 48px;
}

@media (min-width:768px) {
    .brands__show {
        display: block;
        position: relative;
        flex-grow: 1;
        margin-top: 17px;
        margin-left: 50px;
    }

    .brands .brands .brands__show {
        margin-top: 55px;
    }

    .brands__show>button {
        border: none;
        background-color: inherit;
    }
}