.services__information {
    position: relative;
}

.services-information-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (min-width:768px) { 
    .services__information {
        display: flex;
        justify-content: flex-start;
        padding: 15px;
        color: #1B1C21;
        margin-left: 10px;
    }
}

@media (min-width:1120px) {
    .services__information {
        margin-top: -21px;   
    }
}