.footeritems__buttons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.footeritems__buttons a {
    margin-left: 20px;
}

.footeritems__buttons a:first-child {
    margin-left: 0;
}

.button--modified{
    transition: all 0.2s ease-out;
}

.button--modified:hover {
    filter:drop-shadow(0px 0px 3px rgba(13, 15, 24, 0.8));
}