.services__description {
    margin-top: 25px;
    margin-bottom: 35px;
}

.services__description p:first-child {
    font-size: 17px;
    width: 90%;
    height: auto;
}

.services__description p:nth-child(2){
    display: none;
}

.services__description p:nth-child(2) span {
    display: none;
}

@media (min-width:768px) {

    .services__description p:first-child {
        font-size: 17px;
        width: 100%;
        height: auto;
    }
    
    .services__description p:nth-child(2) {
        display: block;
        font-size: 17px;
        margin-top: 10px;
    }

    .services__description {
        margin-top: 0px;
    }
}

@media (min-width:1120px) {

    .services__description p:first-child {
        font-size: 17px;
        width: 560px;
        height: 110px;
    }
    
    .services__description p:nth-child(2) {
        display: block;
        font-size: 17px;
        margin-top: 10px;
        width: 560px;
        margin-top: -15px;
        margin-bottom: 40px;
    }

    .services__description p:nth-child(2) span {
        display: inline;
    }
}