
.CPS-footer{
    display: flex;
    margin-top: 50px;
    border-top: 1px solid #D9FFF5;
    padding-top: 20px;
    padding-right: 7px;
    justify-content: space-between;
    color: #7E7E82;
}

.CPS-footer div {
    min-width: 220px;
    max-width: 330px;
    margin-left: 7px;
}

@media (max-width: 768px) {


    .CPS-footer{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .CPS-footer div {
        margin-bottom: 15px;
        text-align: center;
    }

}
