.leftitems-services {
    display: none;
    width: 400px;
    justify-content: flex-end;
    margin-left: 20px;
}

.leftitems-services__item {
    margin-left: 10px;
}


@media (min-width:768px) {
    .leftitems-services {
        display: flex;
    }
}

@media (min-width:1120px) {
    .leftitems-services {
        display: none;
    }
}